import http from '@/components/utils/http.js';
export default {
	data() {
		return {
			payType: "wx",
			payamount:0.00,
			orderInfo: {},
			oid:"",
		};
	},
	computed: {
	
	},
	onLoad(options) {
	
		let oid=options.oid;
		if(oid=="undefined"){
			uni.showToast({ title: "订单数据错误", icon: 'none' });
			return
		}
		this.oid=oid;
		this.getorderinfo(oid);
		
	},

	methods: {
		getorderinfo(oid){
			let _that=this;
			let opts = {
				url: '/orderApi/payorder/',
				method: 'post'
			};
			
			let param = {oid:oid};
			_that.$http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						_that.payamount=res.data['total'];
					} else {
						uni.showToast({ title: res.data.msg, icon: 'none' });
					}
				},
				error => {
					console.log(error);
				}
			);
			
			
		},
		payorder(){
			let _that=this;
			uni.showLoading({
				 title: '支付中...'
			})
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					
					let code = loginRes.code;
					let data={oid:_that.oid,paytype:_that.payType,code:code};
					let opts = {
						url: '/orderApi/payordercreate/',
						method: 'post'
					};
					
					let param = data;
					_that.$http.httpTokenRequest(opts, param).then(
						res => {
							//打印请求返回的数据
							
							console.log(res.data['prepay_order']);
							if(res.data['code']==0){
								setTimeout(function () {
								    uni.hideLoading();
								}, 1000);
								uni.requestPayment({
								   provider: "wxpay",
								   orderInfo:_that.oid,
								   timeStamp:res.data["prepay_order"].timeStamp,
								   nonceStr:res.data["prepay_order"].nonceStr,
								   package: res.data["prepay_order"].package,
								   signType:res.data["prepay_order"].signType,
								   paySign:res.data["prepay_order"].paySign,
								   success(res) {
										uni.redirectTo({
											url:"./paySuccess?oid/"+_that.oid,
										})	
								    },
								    fail(err) {
								       console.log(err)
								    }
								});
							}else{
								setTimeout(function () {
								    uni.hideLoading();
								}, 1000);
								uni.showToast({ title:res.data['msg'] , icon: 'none' });
								
							}
							
						},
						error => {
							console.log(error);
						}
					);
					
				}
			});
		
		},
		
		
